<template>
<div class="question-box-container ">
    <b-jumbotron class="questionDiv">

    <template #lead>
      {{ currentQuestion.question}}
    </template>

    <hr class="my-4">
    <b-list-group-item
     v-for="(answer,index) in answers" :key="index" 
     @click="selectAnswer(index)"
     :class="answerClass(index)">
     {{answer}}
     </b-list-group-item>

    <b-button variant="primary" @click="submitAnswer"
    :disabled = "selectedIndex === null || answered === true" >
      Submit
      </b-button>
    <b-button @click="next" variant="success" >Next</b-button>
  </b-jumbotron>
  </div>
</template>

<script>
// var _ = require('lodash');
import _ from 'lodash'
export default {
    props:{
        currentQuestion : Object,
        next: Function,
        // index: Number
        increment : Function

    },
    data(){
      return{
        selectedIndex : null,
        shuffledAnswers : [],
        correctIndex : null,
        answered: false
      } 
    },
    watch:{
      currentQuestion :{
        immediate : true,
        handler(){
          this.selectedIndex=null
          this.answered = false
        this.shuffleAnswers()
        }
        
      }
    },
    methods:{
      selectAnswer(index){
        this.selectedIndex = index;
        // console.log(index)
      },
      answerClass(index){
        let ans = ''
        if(!this.answered && this.selectedIndex === index )
          ans = 'selected'
        else if(this.answered && this.correctIndex === index )
          ans = 'correct'
        else if(this.answered && this.selectedIndex===index 
        && this.correctIndex !== index )
          ans = 'incorrect'
        else
          ans = ''
        return ans
      },
      shuffleAnswers()
      {
        console.log('shuffle function called')
        let  answers = [...this.currentQuestion.incorrect_answers, this.currentQuestion.correct_answer]
        this.shuffledAnswers = _.shuffle(answers)
        this.correctIndex = this.shuffledAnswers.indexOf(this.currentQuestion.correct_answer)
        console.log(this.shuffledAnswers)
      },
      submitAnswer(){
        let isCorrect = false
        if(this.selectedIndex === this.correctIndex){
          isCorrect = true
        }
        this.increment(isCorrect)
        this.answered = true
      }
    },
    mounted()
    {
        console.log(this.currentQuestion)
        // console.log(this.index)
    },
    computed:{
      answers(){
        let answers = [...this.currentQuestion.incorrect_answers]
       answers.push(this.currentQuestion.correct_answer)
       
      return answers
      }
    }
    
}
</script>

<style scoped>
.list-group-item{
  margin-bottom: 15px;
}
.list-group-item:hover{
  background: rgba(255, 252, 252, 0.445);
  cursor: pointer ;
}
.selected{
  background-color: rgba(89, 89, 255, 0.651);
}
.correct{
  background-color: rgba(12, 156, 12, 0.685);
}
.incorrect{
  background-color: rgba(243, 71, 71, 0.726);
}
.btn {
  margin:0 5px ;
}

/* .questionDiv {
  background-color: rgba(253, 253, 253, 0);
} */
</style>